import Vue from 'vue'
import Router from 'vue-router'
import IndexPage from '../components/index/index'

Vue.use(Router)

export const RouterMapMobile = [
  {
    path: '/',
    component: IndexPage,
    name: '首页'
  },
  {
    path: '/',
    redirect: '/'
  }
]
let router = {
  mode: 'history', // 后端支持可开
  routes: RouterMapMobile
}
if (window.location.host.indexOf('localhost') > -1) {
  router = {
    routes: RouterMapMobile
  }
}
export default new Router({
  ...router
})
